<template>
	<view class="shop">
		<!-- 搜索框 -->
		<view class="searchBox height" >
			<navigator url="" class="serach" @click="goSearch">
				<u-icon color="#999" size="40" name="search"></u-icon>
				<span style="color:#999;" >搜索</span>
			</navigator>
			<!-- 分类 -->
			<navigator class="tab_fenlei" url="/pages/shopClassify/shopClassify" >
				<u-icon label="分类" label-pos="bottom" label-size="24" 
					margin-top="10" size="50" name="grid-fill" color="white" label-color="white"
					>
				</u-icon>
			</navigator>
		</view>
		
		<!-- 背景图 -->
		<view class="bg"></view>
		
		<!-- 限制内容的区域 让其在中部体现 -->
		<view class="content">
			<!-- 轮播图 -->
			<view class="swiper_box">
				<swiper class="swiper" 
					:autoplay="true" :interval="3000" :duration="500"
					:indicator-dots="false" circular="true" 
					@change="currentChange"
				>
					<swiper-item :style="{'background-color': item.background}" 
						class="swiperitem"
						v-for="(item,index) in swiperImg"
						:key="index"
					>
						<image  :src="item.src" mode="scaleToFill"></image>
					</swiper-item>
				</swiper>
				
				<!-- 自定义轮播图的 指示点 -->
				<view class="current">
					{{current}}/3
				</view>
			</view>
			
			<!-- 购物车 图标 -->
			<view class="shop_car_box">
				<image @click="goshopCar" class="shop_car" src="/static/store/car.png" mode=""></image>
			</view>
			
			<!-- 分类图标 -->
			<view class="cate-section">
				<view class="cate-item">
					<image src="/static/store/c3.png"></image>
					<text>环球美食</text>
				</view>
				<view class="cate-item">
					<image src="/static/store/c5.png"></image>
					<text>个护美妆</text>
				</view>
				<view class="cate-item">
					<image src="/static/store/c6.png"></image>
					<text>营养保健</text>
				</view>
				<view class="cate-item">
					<image src="/static/store/c7.png"></image>
					<text>家居厨卫</text>
				</view>
				<view class="cate-item">
					<image src="/static/store/c8.png"></image>
					<text>速食生鲜</text>
				</view>
			</view>
			
			<!-- 特惠专场 -->
			<view class="Good_notes">
			  <view class="Good_notes_title">
			    <view class="title_text">特惠专场</view>
			    <navigator hover-class="none" class="Good_notes_del" 
						url="/packageA/pages/categorylist/list?type=1&materialid=4094&title=特惠专场"
						>
			      <text>更多</text>
			      <text class="yticon icon-you"></text>
			    </navigator>
			  </view>
			  <swiper class="Good_goods_container" :autoplay="true" :interval="5000" :duration="1000" :circular="true">
			    <swiper-item class="Good_goods" v-for="(item,index) in [1,2,3]" :key="index">
			      <navigator hover-class="none" class="Good_goods_item" 
							v-for="(item,idx) in [1,2,3] "  :key="idx"
							>
			        <image class="Good_goods_img" 
								src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1553187020783&di=bac9dd78b36fd984502d404d231011c0&imgtype=0&src=http%3A%2F%2Fb-ssl.duitang.com%2Fuploads%2Fitem%2F201609%2F26%2F20160926173213_s5adi.jpeg" 
								/>
			          <view class="Good_goods_img_tag">券22元</view>
			        <view class="Good_goods_price">券后￥33</view>
			      </navigator>
			    </swiper-item>
			  </swiper>
			</view>
			
			<!-- 商品瀑布流的渲染 -->
			<shop-waterfall :list="goods" ></shop-waterfall>
			
			<!-- 商品分类 -->
				<!-- 头部类别展示 -->
				<!-- <swiperTabHead class="height" :flex="true" 
					:top="tabTop" :tabBars="tabBars" :tabIndex="tabIndex" 
					@tabtap="tabtap"
					>
					
				</swiperTabHead> -->
				
				<!-- 相应的商品展示 -->
				<!-- <swiper :style="{height:swiper_height}" :current="tabIndex" @change="tabChange">
					<swiper-item   style="box-sizing: border-box;padding: 0 10rpx;" v-for="(tab,i) in tabBars" :key="i">
						<mescroll-item :showType="2" :waterFullHeight="swiper_height" :i="i" :index="tabIndex" :tabs="tabBars"></mescroll-item>
					</swiper-item>
				</swiper> -->
		</view>
	</view>
</template>

<script>
	import Json from '../../Json.js' //测试用数据
	// 引入商品瀑布流
	import shopWaterfall from "@/components/shop-waterfall.vue"
	// import QrcodePoster from '/components/zhangyu-qrcode-poster.vue'
	import MescrollItem from "@/components/mescroll-swiper-item.vue";
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	// 引入tabHead 切换
	import swiperTabHead from "@/components/swiper-tab-head.vue";
	export default {
	    data() {
				return {
					goods:[],// 商品列表
					swiperImg:[],//swiper轮播图图片
					// 轮播图的指示点
					current:1,
					tabBars:[
						{ name:"推荐",id:"guanzhu" },
						{ name:"配饰",id:"tuijian" },
						{ name:"服装",id:"tuijian" },
						{ name:"包包",id:"tuijian" },
						{ name:"内衣",id:"tuijian" },
					], 
					tabIndex:0,
					swiper_height: "", // 需要固定swiper的高度
					height:0,
					tabTop:0,
				}
	    },
			components:{
				shopWaterfall,
				swiperTabHead,
				MescrollItem,
			},
			onLoad() {
				// 获取商品的 展示列表
				this.loadData();
				
				let _this = this
				// 设置滚动区域的高度
				uni.getSystemInfo({
					success(res) {
						// console.log('页面信息res',res)
							_this.phoneHeight = res.windowHeight; //获取用户设备的高度
							console.log('用户的设备高度',res.windowHeight);
							// 计算组件的高度
							let view = uni.createSelectorQuery().selectAll('.height');
							view.boundingClientRect(data => {
								// 计算上方各元素的高度总和
								data.forEach((item,index) => {
									// console.log('item',item.height)
									_this.height += parseInt(item.height)
								})
									_this.swiper_height =  _this.phoneHeight - _this.height ;
									// console.log('_this.height',_this.height)
									_this.swiper_height =  _this.swiper_height +"px";
							}).exec();
					}
				});
				
				// 获取搜索框的高度,进而决定，分类列表头部距离顶部的粘性定位
				let view = uni.createSelectorQuery().selectAll('.searchBox');
				view.boundingClientRect(data => {
					// 计算上方各元素的高度总和
					data.forEach((item,index) => {
						// console.log('item',item.height)
						_this.height += parseInt(item.height)
					})
						_this.tabTop =  _this.height ;
				}).exec();
			},
			onReady() {
				// 动态设置滚动区域的高度
				let _this = this 
				uni.getSystemInfo({
					success(res) {
						// console.log('页面信息res',res)
							_this.phoneHeight = res.windowHeight; //获取用户设备的高度
							// console.log(res.windowHeight);
							// 计算组件的高度
							let view = uni.createSelectorQuery().selectAll('.height');
							view.boundingClientRect(data => {
								// 计算上方各元素的高度总和
								data.forEach((item,index) => {
									console.log('item',item.height)
									_this.height += parseInt(item.height)
								})
									_this.height = _this.height + 'px'
							}).exec();
					}
				});
			},
	    methods: {
				// 获取商品列表中的数据
				// getgoods(type){
				// 	return new Promise(resolve=>{
				// 		setTimeout(()=>{
				// 			resolve(Json[type]);
				// 		}, 500)
				// 	})
				// },
				loadData(){
					// 获取商品列表中的数据
					 this.goods = Json['goodsList'];
					 // 获取轮播图中的数据
					 this.swiperImg = Json['carouselList']
				},
				// 监控轮播图的变化
				currentChange(e){
					// console.log('轮播图变化',e.detail.current)
					this.current = e.detail.current + 1
				},
				// 跳转到购物车页面
				goshopCar(){
					uni.navigateTo({
						url:"/pages/shopcar/shopcar"
					})
				},
			// 控制tab切换
				//接受子组件传过来的值点击切换导航
				tabtap(index){
					console.log('点击切换')
					this.tabIndex = index;
				},
				//滑动切换导航
				tabChange(e){
				  this.tabIndex = e.detail.current
				},
	    }
	}
</script>

<style lang="scss">
	.shop{
		// 搜索框
		.searchBox{
			width: 100%;
			height: 83rpx;
			background-color: #f83f20;
			padding: 20rpx 0;
			position: sticky;
			z-index: 10;
			top: 0;
			left: 0;
			display: flex;
			align-items: center;
			box-sizing: border-box;
			padding: 10rpx 20rpx;
			margin-bottom: 10px;
			.serach{
				background-color: white;
				border-radius: 33rpx;
				width: 100%;
				height: 100%;
				text-align: center;
				color: #453a74;
				display: flex;
				align-items: center;
				justify-content: center;
				image{
					width: 40rpx;
					height:40rpx ;
					margin-right: 13rpx;
				}
			}
			.tab_fenlei{
				margin-left: 18rpx;
				image{
					width: 48rpx;
					height: 48rpx;
				}
			}
		}
		
		// 背景图
		.bg{
			width: 100%;
			height: 160px;
			background-color: #f83f20;
			position: absolute;
			top: 0;
			left: 0;
			border-radius: 0 0 20rpx 20rpx;
		}
		
		// 内容区域
		.content{
			box-sizing: border-box;
			padding: 0 20rpx;
			// 轮播图 盒子
			.swiper_box{
				position: relative;
				border-radius: 25rpx;
				overflow: hidden;
				.swiper{
					.swiperitem{
						image{
							width: 100%;
							height: 100%;
						}
					}
				}
				.current{
					width: 56rpx;
					height: 44rpx;
					font-size: 27rpx;
					position: absolute;
					bottom: 10px;
					right: 11px;
					background-color: #f83f20;
					color: white;
					border-radius: 20rpx;
					box-sizing: border-box;
					padding: 7rpx 10rpx;
					opacity: 0.8;
				}
			}
			// 购物车图标
			.shop_car_box{
				width: 73rpx;
				height: 73rpx;
				background-color: #f83f20;
				border-radius: 50%;
				overflow: hidden;
				position: fixed;
				bottom: 33rpx;
				right: 40rpx;
				z-index: 20;
				opacity: 1;
				.shop_car{
					width: 100%;
					height: 100%;
				}
			}
			// 分类图标
			.cate-section {
				display: flex;
				justify-content: space-around;
				align-items: center;
				flex-wrap:wrap;
				padding: 30upx 22upx; 
				background: #fff;
				.cate-item {
					display: flex;
					flex-direction: column;
					align-items: center;
					font-size: 26upx;
					color: #303133;
				}
				image {
					width: 88upx;
					height: 88upx;
					margin-bottom: 14upx;
					border-radius: 50%;
					opacity: .7;
					box-shadow: 4upx 4upx 20upx rgba(250, 67, 106, 0.3);
				}
			}
			/* 特惠专场 */
			.Good_notes {
			  box-sizing: border-box;
			  width: 690rpx;
			  padding: 0 18rpx 42rpx 18rpx;
			  border-radius: 10rpx;
			  background: white;
				.Good_notes_title {
				  display: flex;
				  align-items: center;
				  justify-content: space-between;
				  height: 102rpx;
					.title_text {
					  font-size: 34rpx;
					  color: #333333;
					  font-weight: bold;
					}
					.Good_notes_del {
					  color: #999;
					  font-size: 24rpx;
					}
				}
				.Good_goods_container {
				  width: 100%;
				  height: 266rpx;
					.Good_goods {
					  display: flex;
					  width: 654rpx;
					  height: 266rpx;
						.Good_goods_item {
						  width: 210rpx;
						  height: 266rpx;
						  margin-right: 10rpx;
							.Good_goods_img {
							  position: relative;
							  display: block;
							  width: 210rpx;
							  height: 210rpx;
							  border-radius: 10rpx;
							  overflow: hidden;
							}
							.Good_goods_img_tag {
							  position: absolute;
							  left: 0;
							  bottom: 80rpx;
							  width: 88rpx;
							  height: 30rpx;
							  border-radius: 0 15rpx 15rpx 0;
							  background: #f83f20;
							  text-align: center;
							  line-height: 30rpx;
							  font-size: 20rpx;
							  color: white;
							}
							.Good_goods_price {
							  margin-top: 14rpx;
							  font-size: 28rpx;
							  color: #ff4020;
							}
						}
					}
				}
			}
		}
	}
</style>
